body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  color: #666666;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  background: url("../img/triangle1.png") left 21.7% bottom 235px no-repeat;
  background-color: #ffffff;
}
.visually-hidden:not(:active):not(:focus),
input[type="checkbox"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  overflow: hidden;
}
@media (min-width: 501px) {
