  }
}
.page-header__wrapper {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}
.page-header {
  box-sizing: border-box;
  display: flex;
  padding: 17px;
  padding-right: 15px;
}
.page-header__logo-subscription {
  display: none;
}
.page-header__navigation {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  z-index: 20;
}
.page-header__contacts {
  display: flex;
  box-sizing: border-box;
  width: 25.2%;
  margin-left: 9.5%;
  padding-bottom: 10px;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
}
.page-header__menu-toggle {
  position: relative;
  flex-shrink: 0;
  width: 37px;
  margin-bottom: 10px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.page-header__menu-toggle::before {
  position: absolute;
  content: "";
  top: 10px;
  left: 0;
  width: 37px;
  height: 1px;
  background-color: #ffffff;
  transform-origin: top left;
  transition: transform 1s ease;
}
.page-header__menu-toggle::after {
  position: absolute;
  content: "";
  top: 22px;
  left: 0;
  width: 37px;
  height: 1px;
  background-color: #ffffff;
  transform-origin: bottom left;
  transition: transform 1s ease;
}
.page-header__menu-toggle--opened::before {
  transform: rotate(20deg);
}
.page-header__menu-toggle--opened::after {
  transform: rotate(-20deg);
}
@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
.page-header__menu {
  position: absolute;
  box-sizing: border-box;
  width: 150%;
  margin: 0;
  padding: 10px;
  list-style: none;
  top: 50px;
  left: 0;
  color: #ffffff;
  background-color: #00b2ff;
  z-index: 15;
  animation: bounce 0.8s;
}
.page-header__menu--hidden {
  display: none;
}
.page-header__menu-item a {
  display: block;
  margin-bottom: 5px;
  padding: 7px;
  background-color: #19baff;
  color: #ffffff;
  text-decoration: none;
}
.page-header__menu-item a:active {
  background-color: #01aaff;
}
.page-header__mail-link {
  position: relative;
  flex-shrink: 0;
}
.page-header__mail-link::before {
  content: "";
  position: absolute;
  top: -9px;
  left: 0;
  width: 26px;
  height: 19px;
  background-image: url("../img/envelope-icon.png");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.page-header__mail-link span {
  display: none;
}
.page-header__phone {
  position: relative;
  flex-shrink: 0;
  margin-left: 25px;
}
.page-header__phone::before {
  content: "";
  position: absolute;
  top: -13px;
  left: -25px;
  width: 25px;
  height: 24px;
  background-image: url("../img/phone-icon.png");
  background-repeat: no-repeat;
  background-position: 0 0;
}
